/* eslint-disable */
import Prism from "prismjs";
//  css     
import "prismjs/themes/prism.css";
import Swal from "sweetalert2";
import "@toast-ui/editor/dist/toastui-editor.css";
import { Editor } from "@toast-ui/react-editor";

import "@toast-ui/editor-plugin-code-syntax-highlight/dist/toastui-editor-plugin-code-syntax-highlight.css";
import codeSyntaxHighlight from "@toast-ui/editor-plugin-code-syntax-highlight";

import "tui-color-picker/dist/tui-color-picker.css";
import "@toast-ui/editor-plugin-color-syntax/dist/toastui-editor-plugin-color-syntax.css";
import colorSyntax from "@toast-ui/editor-plugin-color-syntax";
import React, { useState, useRef } from "react";
import { Grid, Button } from "../elements/Index";
import { apis } from "../lib/axios";

import { useSelector, useDispatch } from "react-redux";
import { history } from "../redux/configureStore";

export default function Writer(props) {
  const dispatch = useDispatch();
  const editorRef = useRef();

  const [progress, setProgress] = useState(0);
  const [selectedFile, setSelectedFile] = useState(null);
  const [imgBase64, setImgBase64] = React.useState([]); //  base64
  const [imgData, setImageData] = React.useState();
  const [imgurl, setImgurl] = React.useState();
  const [text, setText] = React.useState();
  const [mydata, setMydata] = React.useState();

  //  
  const uploadImage = (blob, callback) => {
    let reader = new FileReader();
    reader.readAsDataURL(blob); // 1.    .
    //   
    reader.onloadend = () => {
      // 2.    .
      const base64 = reader.result;
      // console.log(base64);
      setImgBase64(base64);
      // console.log("1 ", imgBase64);

      const upload = async () => {
        // console.log("2 ", base64);
        //    
        const url = await apis.addMyImage(base64);

        // const resulturl = `https://scopewith.com${url.data.data.imageUrl}`;
        const resulturl = url.data.data.imageUrl;
        callback(resulturl, "alt text");
      };
      upload();
    };
  };
  // http://15.165.159.211
  React.useEffect(() => {
    const userId = props.location.state.userId;
    const fetchData = async () => {
      try {
        const result = await apis.getMypage(userId);

        setMydata(result.data.data);
      } catch (err) {}
    };
    fetchData();
  }, []);

  const onChangeEditorTextHandler = () => {
    setText(editorRef.current.getInstance().getMarkdown());
  };

  // 
  const write = () => {
    const writing = async () => {
      try {
        const userId = props.location.state.userId;
        const result = await apis.writeMyIntroduction(userId, text);

        history.push(`/mypage/${userId}`);
      } catch (err) {
        const userId = props.location.state.userId;

        // window.alert("   .");
        Swal.fire("  !", "", "question");
      }
    };
    writing();
  };

  const introduction = mydata?.user.introduction;
  return (
    <Grid maxWidth="1400px" margin="auto">
      {introduction === null && (
        <>
          <button
            onClick={() => {
              write();
            }}
          >
            
          </button>

          <Editor
            previewStyle="vertical"
            plugins={[
              colorSyntax,
              [codeSyntaxHighlight, { highlighter: Prism }],
            ]}
            onChange={onChangeEditorTextHandler}
            ref={editorRef}
            height="79vh"
            // initialValue={introduction}
            hooks={{
              addImageBlobHook: async (blob, callback) => {
                uploadImage(blob, callback);
                // callback(img_url, "alt_text");
              },
            }}
          ></Editor>
        </>
      )}
      {introduction && (
        <>
          <Editor
            previewStyle="vertical"
            plugins={[
              colorSyntax,
              [codeSyntaxHighlight, { highlighter: Prism }],
            ]}
            onChange={onChangeEditorTextHandler}
            ref={editorRef}
            height="79vh"
            initialValue={introduction}
            hooks={{
              addImageBlobHook: async (blob, callback) => {
                uploadImage(blob, callback);
                // callback(img_url, "alt_text");
              },
            }}
          ></Editor>
          <Grid display="flex" margin="0 0 0 72% ">
            <Button
              width="100px"
              height="50px"
              margin="20px"
              _onClick={() => {
                history.goBack();
              }}
            >
              
            </Button>
            <Button
              width="100px"
              height="50px"
              margin="20px"
              _onClick={() => {
                write();
              }}
            >
              
            </Button>
          </Grid>
        </>
      )}
      {!introduction && (
        <>
          <Grid display="flex" margin="0 0 0 72% "></Grid>
          <Editor
            previewStyle="vertical"
            plugins={[
              colorSyntax,
              [codeSyntaxHighlight, { highlighter: Prism }],
            ]}
            onChange={onChangeEditorTextHandler}
            ref={editorRef}
            height="79vh"
            initialValue={introduction}
            hooks={{
              addImageBlobHook: async (blob, callback) => {
                uploadImage(blob, callback);
                // callback(img_url, "alt_text");
              },
            }}
          ></Editor>
          <Grid display="flex" margin="0 0 0 2% ">
            <Button
              width="100px"
              height="50px"
              margin="20px"
              _onClick={() => {
                history.goBack();
              }}
            >
              
            </Button>
            <Button
              width="100px"
              height="50px"
              margin="20px"
              _onClick={() => {
                write();
              }}
            >
              
            </Button>
          </Grid>
        </>
      )}
    </Grid>
  );
}
